body {
	align-items: center;
	display: inline-table;
	margin:0px;
	background-color: rgb(255, 111, 0);
	font-family: sans-serif;
}
.calculator .container{
    height:100px;
	width: 70px;
    display:flex;
}
.calculator{
	background-color:rgba(251, 106, 239, 0.885);
	text-align:justify;
	margin-top: 125px;
	margin-right: 340px;
	margin-left: 650px;
	padding:10px;
	padding-left:10px;
	padding-right: 30px;
	border-radius: 30px;
	box-shadow: inset 15px 15px 10px rgba(216, 87, 220, 0.597),
	                  15px 15px 20px rgba(247, 235, 235, 0.952);
    display:grid;
    grid-template-columns: repeat(4,60px);
}
.calculator input{
	grid-column: span 4;
	height:60px;
	width:220px;
	background-color: rgb(216, 224, 125);
	box-shadow: inset -5px -5px 12px rgba(30, 3, 3, 0.349),
	            inset  5px 5px 12px rgba(0, 0, 0, 0.501);
    border:none;
    border-radius: 30px;
    text-align: end;
    font-size: 40px;
    margin:auto;
    margin-top:30px;
	margin-left: 10px;
    margin-bottom:30px;
    padding:20px;
}
.calculator button{
	height:40px;
	width:50px;
	background-color: rgb(245, 245, 245);
	box-shadow: inset -5px -5px 12px rgb(137, 182, 181),
	            inset  5px 5px 12px rgba(0, 0, 0, 0.2);
    border:none;
    border-radius:30px;
    margin:8px;
    font-size: 20px;
}
.calculator .equal{
	width:110px;
	border-radius:40px;
	background-color: rgb(232, 232, 232);
	
}