body {
    background-color: rgb(18, 52, 223);
    position:sticky;
     display: flex;
     /* background-repeat: no-repeat; */
     background-attachment: fixed;
     background-size: cover;
     background-position: center;
 }

 .signupform {
   background-color: rgb(81, 67, 67);
    border: 5px solid #9900db;
    box-shadow: 5px 5px 5px rgba(255, 255, 255, 0.545);
    width: 300px;
    height: 150px;
    padding: 10px;
    margin-top: 20%;
    border-left:10px solid rgb(86, 254, 2);
    border-right:10px solid rgb(0, 255, 26);
    border-bottom:10px solid rgb(0, 255, 21);
    border-top:10px solid rgb(0, 255, 13);
    padding: 40px;
    padding-left: 30px;
    padding-right: 56px;
    padding-top: 33px;
    padding-bottom: 50px;
    margin-top: 200px;
    margin-bottom: 250px;
    margin-left: 600px;
    margin-right: 65px;
 }

 .signupform button {
    background-color: #fffb20f3;
    color: rgb(0, 0, 0);
    width: 100px;
    height: 40px;
  }
  .signupform input {
   width: 96%;
 }
