table, th, td {
    border: 2px solid black;
    border-collapse: collapse;
    background-color: rgb(211, 41, 75);
}
th {
    padding: 3px;
    background-color: aqua;
    color: black;
}
td {
    padding: 4px;
}
tr:hover {
    background-color: rgb(10, 10, 10);
}
.buttons button.active {
    padding: 6px;
    background-color: lightblue;
    color: rgb(0, 0, 0);
  }

  .buttons button:disabled {
    opacity: 0.3;
  }